import React from 'react'
import {FaMapMarkerAlt} from 'react-icons/fa'
function RobotServing({robot, clickedRobotId, onClick}) {
    // if (robot.current_node == robot.final_node) return null;
    var stateString = "이동중"
    if (robot.state == 2) return null;  // 물건 없음
    else if(robot.current_node == robot.final_node) stateString="서빙완료";
    const robotImgSrc = "/assets/robot/octa_robot_white.png";
    var statusColor = "#ffffff00";
    if(robot.robot_id == clickedRobotId){
        statusColor = "#0000ff11";
    }
    return (
        <li id="robot-serving-item" onClick={onClick} style={{ backgroundColor:statusColor}}>
            <img src={robotImgSrc} alt="robot-icon-img" className='robot-icon-img float' />
                <div id = "serving-info" className="float">
                    <span id="arrive-robot-string" className="text-color-1"> 로봇 : </span>

                    <span id="arrive-robot" className="text-color-1" >{robot.robot_id}</span>
                </div>
                <div id = "serving-info" className="float">
                <span id="arrive-node" className="text-color-2">{robot.table_name}</span>
                <span id="arrive-robot-string" className="text-color-1"> 위치 {stateString}</span>
                {stateString == "이동중" ?
                    <div id="arrow-div">
                        <div id="arrow-right-div">
                        <p id="arrow-right"></p>
                        <p id="arrow-right"></p>
                        </div>
                    </div>
                    : <FaMapMarkerAlt className="icon icon-w" />
                }
            </div>
        </li>
    )
}

export default RobotServing
