import React from 'react'

function Table(props) {
    const tableName = props.data['table_name'];
    const posX = (props.data['x']) * 0.01 - 2.4;
    const posY = (props.data['y']) * 0.01 - 1.6;

    return (
        <div className="table-button background-color-1 " onClick={props.onClick} style={{left:posX+"rem", top:posY+"rem"}}>
            <div className="table-button-name text-color-w">{tableName}</div>
        </div>
    )

}

function TablePosition(props) {
    
    const xytypeList = props.xytypeList;

    return (
        <div>
            {xytypeList.map(xytype => (
                <Table data={xytype} key={xytype['table_id']} onClick={() => props.onClick([xytype['table_id'], xytype['table_name']])}/>
            ))}
        </div>
    )
}

export default TablePosition
